.NavBar-display {
  align-content: center;
  background-color: #6c89b3;
  border: 1px solid black;
  display: flex;
  flex-flow: row wrap;
  height: 30px;
  justify-content: space-evenly;
  list-style-type: none;
  margin-top: 5px;
  position: fixed;
  width: 100%;
}

.NavBar-link {
  color: white;
  text-decoration: none;
}

.NavBar-link:hover {
  color: black;
}
